import { hideVisually } from 'polished';
import { FC } from 'react';
import styled from 'styled-components';

interface FeedAnnouncerProps {
  children: string;
}

const StyledFeedAnnouncer = styled.span`
  ${hideVisually}
`;

const FeedAnnouncer: FC<FeedAnnouncerProps> = ({ children }) => {
  return <StyledFeedAnnouncer aria-live='polite'>{children}</StyledFeedAnnouncer>;
};

export default FeedAnnouncer;
