import { FC, forwardRef, Ref, useContext, useMemo, useRef, useEffect, KeyboardEvent } from 'react';
import styled from 'styled-components';

import {
  ForwardProps,
  OmitStrict,
  Button,
  Icon,
  Text,
  Avatar,
  MetaList,
  Count,
  Tooltip,
  ModalManagerContext,
  ModalMethods,
  useI18n,
  useConfiguration,
  useElement,
  formatListToLocaleString
} from '@pega/cosmos-react-core';
import BareRoleButton from '@pega/cosmos-react-core/lib/components/Button/BareRoleButton';

import { FeedPostProps } from './FeedPost.types';
import { FeedContext } from './Feed.context';
import FeedButton from './FeedButton';
import FeedModalList, { FeedModalListProps } from './FeedModalList';
import { FeedReplyProps } from './FeedReply.types';

const InlineBareButton = styled(BareRoleButton)`
  display: inline-block;
`;

interface BaseFeedLikeButtonProps
  extends OmitStrict<FeedPostProps['interactionInfo'], 'commentLabel' | 'interactionsEnabled'>,
    Pick<FeedPostProps, 'onLoadMoreLikes' | 'onLikeCountInteraction' | 'id'>,
    Pick<FeedPostProps['info'], 'liked'> {
  showText?: boolean;
}

interface PostFeedLikeButtonProps extends BaseFeedLikeButtonProps {
  variant: 'post';
  onLikeClick: FeedPostProps['onLikeClick'];
}

interface ReplyFeedLikeButtonProps extends BaseFeedLikeButtonProps {
  variant: 'reply';
  onLikeClick: FeedReplyProps['onLikeClick'];
}

export type FeedLikeButtonProps = PostFeedLikeButtonProps | ReplyFeedLikeButtonProps;

export const FeedLikeButton: FC<FeedLikeButtonProps & ForwardProps> = forwardRef(
  (props, ref: Ref<HTMLButtonElement>) => {
    const {
      id,
      showText,
      onLikeCountInteraction,
      onLoadMoreLikes,
      liked,
      likeLabel,
      likeCount = 0,
      likes = []
    } = props;
    const t = useI18n();
    const { locale } = useConfiguration();
    const { create } = useContext(ModalManagerContext);
    const { announce, userInfo: loggedInUser } = useContext(FeedContext);
    const [countEl, setCountEl] = useElement(null);
    const likesModalRef = useRef<ModalMethods<FeedModalListProps>>();

    useEffect(() => {
      likesModalRef.current?.update({
        count: likeCount,
        heading: likeLabel,
        listItems: likes,
        onLoadMore: onLoadMoreLikes
      });
    }, [likeCount, likeLabel, likes, onLoadMoreLikes]);

    const likesAttributes = useMemo(() => {
      const likesList = likes.map(x => x.primary.toString());
      return {
        ariaLabel: likeCount
          ? `${t('feed_liked_by')} ${formatListToLocaleString(likesList, t, locale, {
              count: likeCount
            })}`
          : t('feed_post_likes', [likeCount], { count: likeCount }),
        label: formatListToLocaleString(likesList, t, locale, {
          count: likeCount,
          separator: '\n'
        })
      };
    }, [likes, likeCount]);

    const onLikeCountClick = () => {
      if (likes.length && likeCount > 10) {
        likesModalRef.current = create(FeedModalList, {
          count: likeCount,
          heading: t('feed_likes'),
          listItems: likes,
          onLoadMore: onLoadMoreLikes
        });
      } else {
        onLikeCountInteraction?.();
      }
    };

    useEffect(() => {
      announce(t(`feed_post_${liked ? 'liked' : 'deliked'}`));
    }, [liked]);

    return (
      <Button
        variant='text'
        as={FeedButton}
        onClick={() => {
          const user = {
            id: loggedInUser.username,
            primary: loggedInUser.fullname,
            secondary: loggedInUser.meta ? <MetaList items={loggedInUser.meta} /> : undefined,
            visual: (
              <Avatar size='l' imageSrc={loggedInUser.avatarSrc} name={loggedInUser.fullname} />
            )
          };
          let eventObj;
          if (props.variant === 'post') {
            eventObj = {
              postId: id,
              user
            } as const;
            props.onLikeClick(eventObj);
          } else {
            eventObj = {
              replyId: id,
              user
            } as const;
            props.onLikeClick(eventObj);
          }
        }}
        emphasized={liked}
        ref={ref}
      >
        <Icon name={liked ? 'thumbs-up-solid' : 'thumbs-up'} />
        {showText && <Text>{likeLabel}</Text>}
        {likeCount > 0 && (
          <>
            <InlineBareButton
              ref={setCountEl}
              role={likes.length ? 'button' : undefined}
              onClick={onLikeCountClick}
              onFocus={onLikeCountInteraction}
              onMouseOver={onLikeCountInteraction}
              aria-label={likesAttributes.ariaLabel}
              tabIndex={0}
              onKeyDown={(e: KeyboardEvent) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  onLikeCountClick();
                }
              }}
            >
              <Count>{likeCount}</Count>
            </InlineBareButton>
            <Tooltip target={countEl} showDelay='none' hideDelay='none'>
              {likesAttributes.label}
            </Tooltip>
          </>
        )}
      </Button>
    );
  }
);
