import {
  FC,
  useState,
  useEffect,
  MutableRefObject,
  MouseEvent,
  useRef,
  useContext,
  useMemo
} from 'react';
import styled, { css } from 'styled-components';

import {
  Avatar,
  Button,
  Count,
  Card,
  CardContent,
  CardHeader,
  Flex,
  Icon,
  registerIcon,
  MenuButton,
  SummaryItem,
  Text,
  defaultThemeProp,
  ForwardProps,
  useI18n,
  Progress,
  useOuterEvent,
  MetaList,
  ModalManagerContext,
  SummaryListItem,
  ModalMethods,
  StyledVisual,
  useConfiguration,
  usePrevious
} from '@pega/cosmos-react-core';
import { RichTextEditorState, StyledRichTextEditor } from '@pega/cosmos-react-rte';
import BareButton from '@pega/cosmos-react-core/lib/components/Button/BareButton';
import * as arrowMicroDownIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/arrow-micro-down.icon';
import * as caretLeftIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/caret-left.icon';
import { formatListToLocaleString } from '@pega/cosmos-react-core/lib/utils/utils';

import FeedButton from './FeedButton';
import FeedAttachments from './FeedAttachments';
import { StyledFeedReplyContainer } from './FeedReply';
import FeedContent from './FeedContent';
import FeedReplyInput from './FeedReplyInput';
import { FeedContext, FeedReplyContext } from './Feed.context';
import FeedRichText from './FeedRichText';
import FeedModalList, { FeedModalListProps } from './FeedModalList';
import { FeedLikeButton } from './FeedLikeButton';
import { FeedPostProps } from './FeedPost.types';

registerIcon(arrowMicroDownIcon, caretLeftIcon);

const defaultProps: Partial<FeedPostProps> = {
  info: {
    avatarSrc: '',
    fullname: '',
    username: '',
    timeElapsed: '',
    content: ''
  }
};

export const StyledFeedPost = styled.div(
  ({ theme }) => css`
    ${StyledFeedReplyContainer}:not(:last-child) {
      margin-block-end: calc(2 * ${theme.base.spacing});
    }
  `
);

StyledFeedPost.defaultProps = defaultThemeProp;

export const StyledPostEdit = styled.div(
  ({ theme }) => css`
    ${StyledRichTextEditor} {
      padding: calc(0.5 * ${theme.base.spacing});
    }
  `
);

StyledPostEdit.defaultProps = defaultThemeProp;

export const StyledReplies = styled.div(
  ({ theme }) => css`
    border-top: 0.0625rem solid ${theme.base.palette['border-line']};
    padding: calc(1.25 * ${theme.base.spacing}) calc(2 * ${theme.base.spacing});
  `
);

StyledReplies.defaultProps = defaultThemeProp;

export const StyledPostFooter = styled.div(
  ({ theme }) => css`
    margin-block-start: ${theme.base.spacing};
  `
);

StyledPostFooter.defaultProps = defaultThemeProp;

const FullWidthSummaryItem = styled.div`
  width: 100%;

  ${StyledVisual} {
    align-self: start;
  }
`;

const FeedPost: FC<ForwardProps & FeedPostProps> = (props: ForwardProps & FeedPostProps) => {
  const {
    id,
    info,
    interactionInfo,
    maxContentHeight = Infinity,
    onMouseEnter,
    onMouseLeave,
    onCommentClick,
    onLikeClick,
    onLikeCountInteraction,
    onLoadMoreLikes,
    onRecipientInteraction,
    onLoadMoreRecipients,
    onUserClick,
    onShowMoreClick,
    newReplyField,
    menuItems,
    onEditSubmit,
    edited = false,
    replies = [],
    replyInput,
    replyInputShown,
    replyCount,
    repliesLoading,
    onShowLessReplies,
    onShowMoreReplies,
    ...restProps
  } = props;
  const {
    avatarSrc,
    fullname,
    username,
    timeElapsed,
    content,
    postType = 'public',
    recipients = [],
    recipientsCount = 0,
    attachments,
    liked,
    icon,
    postContext,
    userStatus
  } = info;
  const { commentLabel, interactionsEnabled = true } = interactionInfo;
  const { announce, userInfo: loggedInUser } = useContext(FeedContext);

  const t = useI18n();
  const { locale } = useConfiguration();
  const rteRef = useRef<RichTextEditorState>();
  const [editMode, setEditMode] = useState(false);
  const editTextAreaRef = useRef<HTMLDivElement>(null);
  const [emptyEditText, setEmptyEditText] = useState(false);
  const [postModified, setPostModified] = useState(false);
  const [replyInputOpen, setReplyInputOpen] = useState(false);
  const [replyInputRef, setReplyInputRef] = useState<MutableRefObject<
    RichTextEditorState | undefined
  > | null>(null);
  const { create } = useContext(ModalManagerContext);
  const recipientsModalRef = useRef<ModalMethods<FeedModalListProps>>();
  const prevReplies = usePrevious(replies);
  const suppressNextAnnouncement = useRef(false);

  useEffect(() => {
    if (recipients.length) {
      recipientsModalRef.current?.update({
        count: recipientsCount,
        heading: t('feed_recipients'),
        listItems: recipients as SummaryListItem[],
        onLoadMore: onLoadMoreRecipients
      });
    }
  }, [recipientsCount, recipients, onLoadMoreRecipients]);

  useOuterEvent('mousedown', [editTextAreaRef], () => {
    if (emptyEditText) setEditMode(false);
  });

  useEffect(() => {
    if ((replyInputOpen || replyInputShown) && replyInputRef) {
      replyInputRef.current?.focus();
    }
  }, [replyInputOpen, replyInputShown, replyInputRef]);

  const userVisual = icon ? (
    <Icon name={icon} />
  ) : (
    <Avatar name={fullname} size='l' imageSrc={avatarSrc} status={userStatus} />
  );

  const numReplies = replyCount || replies?.length;

  const postTypeAttributes = useMemo(() => {
    if (postType === 'private') {
      const recipientsList = recipients.map(x => x.primary);
      return {
        icon: 'users-solid',
        ariaLabel: `${t('feed_private_post')} t('feed_recipients')} ${formatListToLocaleString(
          recipientsList,
          t,
          locale,
          { count: recipientsCount }
        )}`,
        label: formatListToLocaleString(recipientsList, t, locale, {
          count: recipientsCount,
          separator: '\n'
        })
      };
    }

    return {
      icon: 'globe',
      ariaLabel: t('feed_public_post'),
      label: t('feed_public_post')
    };
  }, [postType, recipients, recipientsCount]);

  const handleEdit = () => {
    if (!emptyEditText && postModified) {
      onEditSubmit?.({
        postId: id,
        value: rteRef.current?.getPlainText() || ''
      });
      setEditMode(false);
    }
  };

  useEffect(() => {
    if (prevReplies) {
      const difference = prevReplies.length - replies.length;
      if (difference > 0 && !suppressNextAnnouncement.current) {
        announce(t('feed_reply_removed'));
      } else {
        suppressNextAnnouncement.current = false;
      }
    }
  }, [replies]);

  return (
    <FeedReplyContext.Provider value={{ onShowReplyInput: () => setReplyInputOpen(true) }}>
      <Card
        {...restProps}
        as={StyledFeedPost}
        onMouseEnter={() => {
          onMouseEnter?.({ postId: id });
        }}
        onMouseLeave={() => {
          onMouseLeave?.({ postId: id });
        }}
      >
        <CardHeader>
          <SummaryItem
            as={FullWidthSummaryItem}
            visual={
              onUserClick ? (
                <BareButton
                  onClick={(event: MouseEvent) => {
                    onUserClick?.({ postId: id, username }, event);
                  }}
                >
                  {userVisual}
                </BareButton>
              ) : (
                userVisual
              )
            }
            primary={
              <div>
                {onUserClick ? (
                  <Button
                    variant='text'
                    onClick={(event: MouseEvent) => {
                      onUserClick?.({ postId: id, username }, event);
                    }}
                  >
                    <Text variant='h3'>{fullname}</Text>
                  </Button>
                ) : (
                  <Text variant='h3'>{fullname}</Text>
                )}
                {postContext && <Text variant='primary'> {t('feed_context', [postContext])}</Text>}
              </div>
            }
            secondary={
              <Flex container={{ gap: 1, alignItems: 'center' }}>
                <Button
                  variant='text'
                  icon
                  onClick={
                    recipients.length
                      ? () => {
                          if (recipientsCount > 10) {
                            recipientsModalRef.current = create(FeedModalList, {
                              count: recipientsCount,
                              heading: t('feed_recipients'),
                              listItems: recipients as SummaryListItem[],
                              onLoadMore: onLoadMoreRecipients
                            });
                          }
                        }
                      : onRecipientInteraction
                  }
                  onFocus={onRecipientInteraction}
                  onMouseOver={onRecipientInteraction}
                  aria-label={postTypeAttributes.ariaLabel}
                  label={postTypeAttributes.label}
                >
                  <Icon name={postTypeAttributes.icon} />
                </Button>
                <MetaList items={edited ? [timeElapsed, t('edited')] : [timeElapsed]} />
              </Flex>
            }
            actions={
              menuItems ? (
                <MenuButton
                  text='More'
                  menu={{
                    items:
                      onEditSubmit &&
                      typeof content === 'string' &&
                      username === loggedInUser.username
                        ? menuItems.concat([
                            {
                              primary: t('edit'),
                              id: 'edit',
                              onClick: () => setEditMode(true)
                            }
                          ])
                        : menuItems
                  }}
                  icon='more'
                  iconOnly
                  variant='simple'
                />
              ) : undefined
            }
          />
        </CardHeader>
        <CardContent>
          {editMode ? (
            <>
              <StyledPostEdit ref={editTextAreaRef}>
                <FeedRichText
                  onChange={() => {
                    setEmptyEditText(rteRef.current?.getPlainText().trim() === '');
                    setPostModified(rteRef.current?.getPlainText() !== content);
                  }}
                  autoFocus
                  label={t('feed_edit_post')}
                  labelHidden
                  onSubmit={handleEdit}
                  defaultValue={content}
                  ref={rteRef}
                />
              </StyledPostEdit>
              <Flex container={{ justify: 'between', pad: [2, 0, 0] }}>
                <Button
                  onClick={() => setEditMode(false)}
                  aria-label={`${t('cancel')} ${t('edit')} ${id}`}
                >
                  {t('cancel')}
                </Button>
                <Button
                  variant='primary'
                  disabled={emptyEditText || !postModified}
                  onClick={handleEdit}
                  aria-label={`${t('update')} ${t('edit')} ${id}`}
                >
                  {t('update')}
                </Button>
              </Flex>
            </>
          ) : (
            <FeedContent maxContentHeight={maxContentHeight}>{content}</FeedContent>
          )}
          <FeedAttachments attachments={attachments} readOnly />
          <Flex as={StyledPostFooter} container={{ justify: 'between', wrap: 'wrap', gap: 1 }}>
            {interactionsEnabled ? (
              <Flex container={{ gap: 2 }}>
                <Button
                  variant='text'
                  as={FeedButton}
                  onClick={() => {
                    onCommentClick?.({ postId: id });
                    if (!replyInputOpen) {
                      setReplyInputOpen(true);
                    }
                  }}
                >
                  <Icon name='chat' />
                  <Text>{commentLabel}</Text>
                  {numReplies > 0 && <Count>{numReplies}</Count>}
                </Button>
                <FeedLikeButton
                  id={id}
                  variant='post'
                  onLikeClick={onLikeClick}
                  onLikeCountInteraction={onLikeCountInteraction}
                  onLoadMoreLikes={onLoadMoreLikes}
                  liked={liked}
                  showText
                  {...interactionInfo}
                />
              </Flex>
            ) : (
              <div />
            )}
            {(onShowLessReplies || onShowMoreReplies) && (
              <Flex container={{ gap: 0.5 }}>
                {replies.length !== numReplies && (
                  <>
                    <Text>{t('showing_num_of_num', [replies.length, numReplies])}</Text>
                    <span>&bull;</span>
                  </>
                )}
                {onShowMoreReplies && replies.length < numReplies && (
                  <Button variant='link' onClick={() => onShowMoreReplies({ postId: id })}>
                    {t('show_more')}
                  </Button>
                )}
                {repliesLoading && <Progress placement='inline' />}
              </Flex>
            )}
          </Flex>
        </CardContent>
        {!!replies?.length && <StyledReplies>{replies}</StyledReplies>}
        {replyInputShown || replyInputOpen ? (
          <CardContent>
            <FeedReplyInput
              onSetInputRef={({ ref }) => {
                setReplyInputRef(ref);
              }}
              id={id}
              commentLabel={replyInput.commentLabel}
              attachments={replyInput.attachments}
              placeholder={replyInput.placeholder}
              onFilesAdded={replyInput.onFilesAdded}
              onSubmit={e => {
                setReplyInputOpen(false);
                replyInput.onSubmit(e);
              }}
            />
          </CardContent>
        ) : null}
        {onShowLessReplies && !!replies.length && (
          <Flex container={{ justify: 'center', pad: [0, 0, 1], gap: 1 }}>
            <Button
              variant='link'
              onClick={() => {
                onShowLessReplies({ postId: id });
                suppressNextAnnouncement.current = true;
              }}
            >
              {t('show_less')}
            </Button>
            {repliesLoading && <Progress placement='inline' />}
          </Flex>
        )}
      </Card>
    </FeedReplyContext.Provider>
  );
};

FeedPost.defaultProps = defaultProps;

export default FeedPost;
